import React from "react";
import { PayPalButton } from "react-paypal-button-v2";
import axios from "axios";

const Paypal = props => {
  return (
    <div>
      <h1>{props.Informacion}</h1>
      <p>{props.contenido}</p>
      <PayPalButton
        createOrder={(data, actions) => {
          return actions.order.create({
            purchase_units: [
              {
                amount: {
                  currency_code: "USD",
                  value: props.amount
                }
              }
            ]
          });
        }}
        onApprove={(data, actions) => {
          // Capture the funds from the transaction
          return actions.order.capture().then(function(details) {
            // Show a success message to your buyer

            axios.post("/pay", {
              Expediente: props.Expediente,
              address: details.purchase_units[0].shipping.address
            });
            alert(
              "Transaction Procesada correctamente " +
                details.payer.name.given_name
            );

            // OPTIONAL: Call your server to save the transaction
            return fetch("/paypal-transaction-complete", {
              method: "post",
              body: JSON.stringify({
                orderID: data.orderID
                // Expediente: props.Expediente,
                // address: details.purchase_units[0].shipping.address
              })
            });
          });
        }}
        options={{
          clientId:
            "Af5yDCEtWpKPqIxrWoBumV1WovEHltcq6f1YbCO2KCjbST-zGxeCtXp-hG3JC6WJ8NX0BcVR1_LZ5sjp"
        }}
      />
    </div>
  );
};

export default Paypal;
