import React, { Fragment, useState } from "react";
import "../SVG/checkmark.svg";
import axios from "axios";
import Paypal from "./Paypal";
import Deli from "./delivery-truck.svg";
import emai from "./email.svg";
import mail from "./mail.svg";
const CreateProfile = () => {
  const [formData, setFormData] = useState({
    dat: "",
    Expediente: [],
    terror: ""
  });

  const { dat, Expediente, terror } = formData;

  function checkEstatus(ESTATUS) {
    if (ESTATUS === "Producci�n") {
      return (
        <Fragment>
          <h2 className="Status">Está en proceso</h2>

          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress " aria-hidden="true" />
        </Fragment>
      );
    } else if (ESTATUS === "Entrega") {
      return (
        <Fragment>
          <h2 className="Status Listo">Listo</h2>
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-check progress bar" aria-hidden="true" />

          <div>
            <br></br>
            <br></br>
            <img src={Deli} style={{ width: "25%" }} alt="truck"></img>{" "}
            <img src={emai} style={{ width: "25%" }} alt="email"></img>
            <img src={mail} style={{ width: "25%" }} alt="inbox"></img>
            <Paypal
              Informacion="Pagar envio del pasaporte por correo costo $25.95"
              contenido="En caso de error vuelva a intentarlo"
              amount="25.95"
              Expediente={Expediente}
            />
          </div>
        </Fragment>
      );
    } else if (ESTATUS === "Emitido") {
      return (
        <Fragment>
          <h2 className="Status">Emitido Y/O en Oficina Externa</h2>
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />

          <i className="fa fa-check progress bar" aria-hidden="true" />
        </Fragment>
      );
    } else if (ESTATUS === "Investigaci�n") {
      return (
        <Fragment>
          <h2 className="Status warning">Depuración/llamar a pasaporte</h2>
          <p>Tel: 212-768-2480</p>
          <p>Ext(1028,1031,1016)</p>
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress " aria-hidden="true" />
          <i className="fa fa-square progress " aria-hidden="true" />
          <i className="fa fa-square progress " aria-hidden="true" />
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <h2 className="Status">Está en proceso</h2>
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress bar" aria-hidden="true" />
          <i className="fa fa-square progress " aria-hidden="true" />
          <i className="fa fa-square progress " aria-hidden="true" />
          <i className="fa fa-square progress " aria-hidden="true" />
        </Fragment>
      );
    }
  }

  const onChange = e => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const onSubmit = async e => {
    e.preventDefault();
    try {
      const result = await axios.post("/data", {
        cedula: dat
      });

      if (result.data.length > 0) {
        setFormData({ ...formData, Expediente: result.data, terror: "" });
      }
    } catch (err) {
      const errors = err.response.data.error;
      setFormData({ ...formData, terror: errors, Expediente: [] });
    }
  };
  console.log(Expediente);
  return (
    <div>
      <Fragment>
        {" "}
        <div className="container">
          <div className="logo">
            <img src="logo.png" className="logo-img" alt="" />
          </div>
          <h1 className="main-heading">
            Consulado General de la Republica Dominicana en New York
          </h1>
          <p>Consultar el estatus del pasaporte</p>

          <form onSubmit={e => onSubmit(e)} className="form">
            <input
              type="number"
              placeholder="Introduzca el número de cedula"
              name="dat"
              value={dat}
              onChange={e => onChange(e)}
              className="search"
            />

            <input type="submit" value="Buscar " className="submit" />
          </form>

          {terror ? <div className="Error">{terror}</div> : null}
        </div>
      </Fragment>

      <br />

      {Expediente.length > 0 ? (
        Expediente.map(target => {
          return (
            <div className="container2 left" key={target.EXPEDIENTE}>
              <div>
                <div className="information">
                  <div className="basic-info">
                    <h3 className="Nombre">Nombre:</h3>
                    <span className="nombre-info">{target.NOMBRE}</span>

                    <h3 className="Apellido">Apellido:</h3>
                    <span className="nombre-info">{target.APELLIDO}</span>

                    <h3 className="Nombre">CEDULA</h3>
                    <span className="nombre-info">{target.CEDULA}</span>
                  </div>
                  <div className="status">
                    <h3 className="Apellido">#Expediente</h3>
                    <span className="nombre-info">{target.EXPEDIENTE}</span>

                    <h3 className="Apellido">ESTATUS</h3>
                    {checkEstatus(target.ESTATUS)}
                  </div>
                </div>
                <br />
                <div className="separator" />
                <br />
              </div>
            </div>
          );
        })
      ) : (
        <Fragment />
      )}
    </div>
  );
};

//////

export default CreateProfile;
